import Banner from "../../../Banner";
import React from "react";



const ArticleFrom = ({handleChange, categories, handleSubmit, errors, loadingGif}) => {
    return (
        <div>
            <div>
                <Banner backgroundImage={`url(${process.env.PUBLIC_URL}/assets/img/bg-laptop.jpg)`}
                        title={"Write an article"}/>
            </div>
            <main className="main-content">
                <section className="section">
                    <div className="container">
                        <div className="row">
                            <div className="col-12 col-lg-12">
                                <form className="p-30 bg-gray rounded" onSubmit={handleSubmit} data-form="mailer">
                                    <div className="row">
                                        <div className="form-group col-md-12 my-5">
                                            <input type="file" className="form-control" name="image"
                                                   onChange={handleChange}/>
                                            {
                                                errors.image &&
                                                <small className="text-danger">
                                                    {
                                                        errors.image
                                                    }
                                                </small>
                                            }

                                        </div>
                                        <div className="form-group col-12 col-md-6">
                                            <input className="form-control form-control-lg" type="text" name="name"
                                                   placeholder="Title" onChange={handleChange}/>
                                            {
                                                errors.name &&
                                                <small className="text-danger">
                                                    {
                                                        errors.name
                                                    }
                                                </small>
                                            }
                                        </div>
                                        <div className="form-group col-12 col-md-6">
                                            <select name="category" id="x" className="form-control form-control-lg"
                                                    onChange={handleChange}>
                                                <option key={-1} value>Select category</option>
                                                {
                                                    categories && categories.map(category => {
                                                        return (
                                                            <option key={category.id}
                                                                    value={category.name}>{category.name}</option>
                                                        );
                                                    })
                                                }

                                            </select>
                                            {
                                                errors.category &&
                                                <small className="text-danger">
                                                    {
                                                        errors.category
                                                    }
                                                </small>
                                            }
                                        </div>
                                    </div>
                                    <div className="form-group">
                                        <textarea
                                            className="form-control form-control-lg" rows={4}
                                            placeholder="Content" name="content" defaultValue={""}
                                            onChange={handleChange}
                                        />
                                        {
                                            errors.content &&
                                            <small className="text-danger">
                                                {
                                                    errors.content
                                                }
                                            </small>
                                        }
                                    </div>
                                    <div className="text-center">
                                        <button className="btn btn-lg btn-primary w-200" type="submit">
                                            {
                                                loadingGif? <img className="w-20" src={loadingGif} alt=""/>: 'Create Article'
                                            }
                                        </button>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                </section>
            </main>
        </div>

    );
}

export default ArticleFrom;